import { useEffect } from "react";
import { ChooseAdditionalInformationCard } from "../reusable_components/create_booking/choose_additional_information_card";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";

export default function ChooseAdditionalInformation() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto my-4 p-6 text-black bg-white">
        <h2 className="text-start text-black pb-6 font-extrabold text-3xl">
          Choose additional information
          <p className="text-sm font-normal text-slate-800 pt-2">
            Thank you for choosing Reload logistics services as your go to
            service provider.{" "}
            <span className="text-sm text-blue-700">
              All payments must be done upon delivery
            </span>
            !.
          </p>
        </h2>
        <ChooseAdditionalInformationCard />
        <div className="mb-20" />
      </main>
    </LoadPageAnimation>
  );
}
